import React from "react";
import { ResponsiveBar } from "@nivo/bar";
import { useTheme as useNivoTheme } from "@nivo/core";
import { useTheme } from "@material-ui/core";

export default function Skills() {
  const theme = useTheme();
  return (
    <ResponsiveBar
      data={data}
      margin={{ top: 50, right: 130, bottom: 50, left: 60 }}
      padding={0.3}
      valueScale={{ type: "linear" }}
      indexScale={{ type: "band", round: true }}
      colors={theme.palette.secondary.main}
      borderColor={{ from: "color", modifiers: [["darker", 1.6]] }}
      axisTop={null}
      axisRight={null}
      axisBottom={{
        tickSize: 5,
        tickPadding: 5,
        tickRotation: 0,
        renderTick: CustomTick,
        // legend: "Technologies",
        legendPosition: "middle",
        legendOffset: 32,
      }}
      axisLeft={{
        tickSize: 5,
        tickPadding: 5,
        tickRotation: 0,
        renderTick: CustomTick,
        // legend: "Technologies",
        legendPosition: "middle",
        legendOffset: 32,
      }}
      labelTextColor={"white"}
      labelSkipWidth={12}
      labelSkipHeight={12}
      legends={[
        {
          dataFrom: "keys",
          anchor: "bottom-right",
          direction: "column",
          justify: false,
          translateX: 120,
          translateY: 0,
          itemsSpacing: 2,
          itemWidth: 100,
          itemHeight: 20,
          itemDirection: "left-to-right",
          itemOpacity: 0.85,
          symbolSize: 20,
          effects: [
            {
              on: "hover",
              style: {
                itemOpacity: 1,
              },
            },
          ],
        },
      ]}
      animate={true}
      motionStiffness={90}
      motionDamping={15}
    />
  );
}

const CustomTick = (tick) => {
  const nivoTheme = useNivoTheme();
  const theme = useTheme();
  return (
    <g transform={`translate(${tick.x},${tick.y + 22})`}>
      <line
        stroke={theme.palette.primary.main}
        strokeWidth={1.5}
        y1={-22}
        y2={-12}
      />
      <text
        textAnchor="middle"
        dominantBaseline="middle"
        style={{
          ...nivoTheme.axis.ticks.text,
          fill: theme.palette.text.primary,
          fontSize: 10,
        }}
      >
        {tick.value}
      </text>
    </g>
  );
};

const data = [
  { id: "ES6 & React", value: 100 },
  { id: "Nodejs", value: 100 },
  { id: "HTML & CSS", value: 90 },
  { id: "JQuery", value: 50 },
  { id: "Graphql", value: 60 },
  { id: "UX Design", value: 80 },
  { id: "Quality Assurance", value: 80 },
];
